.header {
  /* height: calc(100vh - 6.5rem); */
  height: 100vh;
  position: relative;
  top: 0;
  width: 100%;
  left: 0;
  padding: 0 4rem;
}

.header-content {
  width: 100%;
  height: 100%;
  position: relative;
}

.header-circle {
  height: 40rem;
  width: 40rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header-outer-circle,
.header-inner-circle {
  height: 100%;
  width: 100%;
  border-radius: 50%;
}

.header__spinning-circle {
  position: absolute;
  height: 40rem;
  width: 40rem;
  background: red;
  z-index: -1;
  border-radius: 50%;
  animation-name: spin-right;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  box-shadow: 2px 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.header-outer-circle {
  background: var(--blue-2);
  padding: 2rem;
}

.header-inner-circle {
  background: var(--blue-1);
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-heading {
  color: var(--darkie);
  font-size: 5rem;
  position: absolute;
  left: 30%;
  top: 15%;
  font-weight: 800;
  transform: translate(-30, -15%);
  font-family: "Times New Roman", Times, serif;
}

.header-main-heading {
  font-size: 5rem;
  font-weight: 500;
  color: var(--darkie);
}

.header-lead {
  font-size: 1.5rem;
  color: var(--darkie);
  font-weight: 500;
  margin-bottom: -1rem;
}

.header-title {
  font-size: 2rem;
  font-weight: 600;
  color: var(--darkie);
}

.header-socials {
  position: absolute;
  bottom: 2rem;
  left: 0;
}

.header-icon {
  color: var(--darkie);
}

.header__theme-btn {
  height: 3.5rem;
  width: 3.5rem;
  border: none;
  background: transparent;
  outline: none;
  position: absolute;
  top: 4rem;
  right: 15rem;
  cursor: pointer;
  z-index: 500;
}

.header__theme-icon {
  height: 3rem;
  width: 3rem;
  color: var(--darkie);
  cursor: pointer;
}

.header-logo {
  height: 3.5rem;
  width: 3.5rem;
  position: absolute;
  top: 4.5rem;
  left: 4rem;
}

@media only screen and (max-width: 75em) {
  .header-heading {
    left: 25%;
    transform: translate(-25%, -15%);
  }
}

@media only screen and (max-width: 64em) {
  .header-circle {
    height: 35rem;
    width: 35rem;
  }

  .header-heading {
    font-size: 3.5rem;
  }

  .header-main-heading {
    font-size: 3.5rem;
  }

  .header-lead {
    font-size: 1.4rem;
  }
}

@media only screen and (max-width: 48em) {
  .header-circle {
    height: 25rem;
    width: 25rem;
  }

  .header-heading {
    font-size: 3rem;
  }

  .header-main-heading {
    font-size: 2.5rem;
  }
  .header-lead {
    font-size: 1.2rem;
  }

  .header-heading {
    left: 20%;
    top: 25%;
    transform: translate(-20%, -25%);
  }

  .header-title {
    font-size: 1.4rem;
  }
}

@media only screen and (max-width: 30em) {
  .header {
    padding: 0 2rem;
  }

  .header-circle {
    height: 20rem;
    width: 20rem;
  }
  .header-inner-circle {
    padding-left: 1.5rem;
  }

  .header-heading {
    font-size: 2rem;
  }

  .header-main-heading {
    font-size: 1.8rem;
  }

  .header-heading {
    left: 15%;
    top: 25%;
    transform: translate(-15%, 25%);
  }

  .header-lead {
    font-size: 0.9em;
    margin-bottom: 0;
  }

  .header-title {
    font-size: 1.3rem;
    margin-top: 1rem;
  }

  .header-logo {
    height: 2.5rem;
    width: 2.5rem;
    left: 2rem;
  }
}
