@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap);
:root {
  --blue-1: #e5f2f2;
  --blue-2: #e1eeee;
  --white-color: #fff;
  --light-color: #f8f8f8;
  --dark-color: #212121;
  --darkie: #383838;
  --light-brown: #d9d5c9;
  --light-purple: #c6c5c3;
  --danger: #d9534f;
  --success: #239923;
  --edit: #0808eb;
  --largest-bp: 75em; /* 1200px */
  --large-bp: 64em; /* 1024px */
  --medium-bp: 48em; /* 768px*/
  --small-bp: 30em; /* 480px */
  --smallest-bp: 20em; /* 320px */
}

/* 320px — 480px: Mobile devices
481px — 768px: iPads, Tablets
769px — 1024px: Small screens, laptops
1025px — 1200px: Desktops, large screens
1201px and more —  Extra large screens, TV */

