@-webkit-keyframes line-anime {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes line-anime {
  to {
    stroke-dashoffset: 0;
  }
}

@-webkit-keyframes fill-loader {
  from {
    fill: transparent;
  }
  to {
    fill: var(--dark-color);
  }
}

@keyframes fill-loader {
  from {
    fill: transparent;
  }
  to {
    fill: var(--dark-color);
  }
}

@-webkit-keyframes spin-right {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }

  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes spin-right {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }

  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

.topbar {
