.fileUploadInput {
	display: grid;
	grid-gap: 10px;
	position: relative;
	z-index: 1;
}

.fileUploadInput label {
	display: flex;
	align-items: center;
	color: setColor(primary, 0.5);
	background: setColor(white);
	transition: 0.4s ease;
	font-family: arial, sans-serif;
	font-size: 1.2em;
	font-weight: regular;
}

.fileUploadInput input {
	position: relative;
	z-index: 1;
	padding: 0 gap(m);
	width: 100%;
	min-width: 35rem;
	height: 50px;
	border: 1px solid rgba(0, 0, 0, 0.2);
	font-family: arial, sans-serif;
	font-size: 1rem;
	-webkit-user-select: none;
	        user-select: none;
	cursor: pointer;
	font-weight: regular;
}
.fileUploadInput input[type="file"] {
	padding: 0 gap(m);
}
.fileUploadInput input[type="file"]::-webkit-file-upload-button {
	visibility: hidden;
	margin-left: 10px;
	padding: 0;
	height: 50px;
	width: 0;
}

.fileUploadInput button {
	position: absolute;
	right: 0;
	bottom: 0;
	width: 50px;
	height: 50px;
	line-height: 0;
	-webkit-user-select: none;
	        user-select: none;
	color: white;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--darkie);
	border: 1px solid rgba(0, 0, 0, 0.2);
}
.fileUploadInput button svg {
	width: auto;
	height: 50%;
	margin-right: 0 !important;
}

@-moz-document url-prefix() {
	.fileUploadInput button {
		display: none;
	}
}

