.loader{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.loader-svg{
  -webkit-animation: fill-loader .1s ease forwards 3s;
          animation: fill-loader .1s ease forwards 3s;
}

.animate-line{
  -webkit-animation: line-anime 2s ease forwards;
          animation: line-anime 2s ease forwards;
}

.fill-loader{
  -webkit-animation: fill .1s ease forwards;
          animation: fill .1s ease forwards;
}

