	}
}

.contact-section {
	width: 100%;
	padding: 10rem 4rem 4rem 4rem;
	min-height: 100vh;
}

.contact-content {
	width: 100%;
	display: grid;
	grid-template-columns: 55rem auto;
}

.contact-content .form-section {
	width: 55rem;
	padding: 5rem;
	background: var(--blue-2);
}

.form-section h5 {
	margin-bottom: 2.5rem;
	text-align: center;
	font-size: 1.6rem;
	text-transform: capitalize;
	font-weight: 600;
	color: var(--darkie);
}

.img-section {
	width: 100%;
	align-self: end;
	position: relative;
	height: 50rem;
}

.contact-art {
	display: block;
	width: 100%;
	height: 40rem;
	background: var(--light-purple);
	-webkit-clip-path: polygon(0% 100%, 100% 100%, 100% 60%, 0% 0%);
	        clip-path: polygon(0% 100%, 100% 100%, 100% 60%, 0% 0%);
	z-index: 5;
	display: block;
	position: absolute;
	bottom: 0;
	left: 0;
}

.contact-art-2 {
	display: block;
	position: absolute;
	width: 100%;
	height: 45rem;
	z-index: -2;
	background: var(--light-brown);
	bottom: 0;
	left: 0;
	-webkit-clip-path: polygon(0% 100%, 100% 100%, 100% 55%, 0% 0%);
	        clip-path: polygon(0% 100%, 100% 100%, 100% 55%, 0% 0%);
}

@media only screen and (max-width: 48em) {
	.contact-content {
		width: 100%;
		display: grid;
		grid-template-columns: auto;
	}

	.contact-content .form-section {
		width: 100%;
		padding: 5rem;
		background: var(--blue-2);
	}

	.img-section {
		display: none;
	}

	.contact-art {
		display: none;
	}

	.contact-art-2 {
		display: none;
	}
}

@media only screen and (max-width: 30em) {
	.contact-section {
		padding: 10rem 2rem 4rem 2rem;
	}

  .contact-content .form-section {
	padding: 5rem 2rem;
}
}

.projects-section {
	width: 100%;
	padding: 10rem 4rem 4rem 4rem;
	/* min-height: 100vh; */
}

.projects-heading {
	margin-bottom: 4rem;
}

.projects {
	display: flex;
	flex-direction: column;
	list-style: none;
}

.projects > *:not(:last-child) {
	margin-bottom: 6rem;
}

.project-stacks{
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 2rem;
}

.project-stacks>*:not(:last-child){
	margin-right: 1rem;
}

.project-stack{
	font-size: 1.3rem;
	font-weight: 400;
	color: var(--darkie);
	display: inline-block;
}

.project-stack::first-letter{
	text-transform: uppercase;
}

.project {
	width: 100%;
	display: grid;
}

.project .img-box,
.project .text-box {
	width: 100%;
	height: 45rem;
}

.project-img{
	width: 100%;
	height: 100%;
}

.project .text-box {
	padding-top: 6rem;
}

.project.img-left {
	grid-template-columns: 55% 40%;
	grid-column-gap: 5%;
}

.project.img-left .img-box {
	order: 1;
	background: var(--blue-1);
}

.project.img-left .text-box {
	order: 2;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.project.img-right {
	grid-template-columns: 40% 55%;
	grid-column-gap: 5%;
}

.project.img-right .img-box {
	order: 2;
	background:  var(--blue-1);
}

.project.img-right .text-box {
	order: 1;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	text-align: end;
}

.project-name {
	font-size: 2rem;
	font-weight: 600;
	color: var(--darkie);
	margin-bottom: 2rem;
	text-transform: capitalize;
}

.project-text {
	font-size: 1.4rem;
	font-weight: 400;
	line-height: 1.5;
	color: var(--darkie);
	margin-bottom: 2rem;
}

.project-btn-container {
	width: 100%;
}

.project-link{
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  border: 1px solid rgba(0, 0, 0, 0.2);
  height: 3rem;
  width: 10rem;
}

.project-link span{
  margin-left: 1rem;
  font-size: 1.4rem;
	font-weight: 400;
	line-height: 1.5;
	color: var(--darkie);
}

@media only screen and (max-width: 48em) {
	.project.img-left,
	.project.img-right {
		grid-template-columns: 100%;
		grid-template-rows: repeat(2, auto);
		grid-row-gap: 4rem;
	}

	.project .text-box {
		padding-top: 0;
	}

	.project .img-box {
		height: 35rem;
	}

	.project .text-box {
		height: auto;
	}

	.project.img-right .text-box {
		order: 2;
	}

	.project.img-right .img-box {
		order: 1;
	}

	.project.img-right .text-box {
		align-items: flex-start;
		text-align: start;
	}

	.projects > *:not(:last-child) {
		margin-bottom: 3rem;
	}
}


@media only screen and (max-width: 30em) {
.projects-section {
	padding: 10rem 2rem 4rem 2rem;
}

	.project .img-box {
		height: 30rem;
	}
}
