@keyframes line-anime {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes fill-loader {
  from {
    fill: transparent;
  }
  to {
    fill: var(--dark-color);
  }
}

@keyframes spin-right {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(-360deg);
  }
}
